.Cart-title {
    /* border: 1px solid red; */
    line-height: var(--products-title-height);
    text-transform: uppercase;
    text-align: center;
    font-size: 1.5rem;
    box-shadow: 0 0 3px gainsboro;
}

.Cart-container {
    /* border: 1px solid blue; */
    min-height: calc(100vh - var(--header-height) - var(--products-title-height) - var(--footer-height-pc) - 2px); /* 2px of title */
}

.Cart-table-summary {
    /* border: 1px solid green; */
    display: flex;
}

/* ------------- TABLE --------------- */

.Cart-table {
    /* border: 3px solid black; */
    display: block;
    width: 100%;
    padding: 5px;
}

.Cart-table thead,
.Cart-table tbody {
    display: block;
    width: 100%;
}

.Cart-row {
    /* border: 1px solid green; */
    display: flex;
    width: 100%;
    padding: 5px;
}

.Cart-row.no-title {
    /* border: 1px solid blue; */
    /* border-bottom: 1px solid rgba(100, 100, 100, 0.5); */
    margin-bottom: 10px;
    box-shadow: 0 0 3px silver;
    border-radius: 3px;
}

.Cart-col-product {
    /* border: 1px solid fuchsia; */
    width: calc(70% - 70px);
    display: flex;
}

.Cart-container-img {
    /* border: 1px solid green; */
    width: 100px;
    height: 100px;
}

.Cart-container-img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.Cart-description {
    margin-left: 10px;
}

.Cart-col-qty {
    /* border: 1px solid blue; */
    width: 70px;
}

.Cart-qty {
    border: 1px solid silver;
    width: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Cart-col-price {
    /* border: 1px solid red; */
    width: 15%;
    text-align: right;
}

.Cart-col-amount {
    /* border: 1px solid green; */
    width: 15%;
    text-align: right;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
}

.Cart-icon {
    font-size: 1.5rem;
    color: rgb(48, 48, 48);
}

.Cart-icon:hover {
    cursor: pointer;
}

/* ------------- SUMMARY ------------- */

.Cart-summary {
    /* border: 1px solid black; */
    width: 320px;
    padding: 10px;
}

.Cart-btn-order {
    background-color: black;
    color: white;
    width: 100%;
    margin-top: 20px;
    padding: 10px;
    border-radius: 5px;
}

.Cart-btn-order:hover {
    cursor: pointer;
}

.Cart-clean-cart {
    color: blue;
    text-decoration-line: underline;
}

.Cart-clean-cart:hover {
    cursor: pointer;
}