.ProductsTitle {
    /* border: 1px solid silver; */
    line-height: var(--products-title-height);
    text-transform: uppercase;
    text-align: center;
    font-size: 1.5rem;
    box-shadow: 0 0 3px gainsboro;
}

.Grid {
    min-height: calc(100vh - var(--header-height) - var(--products-title-height) - var(--footer-height-pc) - 2px); /* 2px of title */
    padding: 20px 0; 
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    gap: 30px 10px; /*row column*/
    justify-items: center;    
    justify-content: center;
    box-shadow: 0 0 3px gainsboro;
}

.Product-card {
    width: 220px;
    max-height: 320px;
    box-shadow: 0 0 2px gray;
    position: relative; /* sidebar is z-index=1 */       
}

.Product-card span {
    /* border: 1px solid red; */
    background-color: white;
    padding: 2px 5px;
    border-radius: 3px;
    box-shadow: 0 0 2px black;
    font-size: 0.9rem;
    position: absolute;
    top: 5px;
    left: 5px;
}

.Product-card img {
    object-fit: cover;    
}

.Product-card img:hover {
    transform: scale(1.05);
    box-shadow: 0 0 10px black;
    transition: transform 250ms;
}

.Product-card p {
    text-align: center;
}

.Product-price {
    color: gray;
}

.Product-card img:hover {
    cursor: pointer;
}