:root {
    --header-height: 90px;
    --footer-height-pc: 100px;
    --products-title-height: 3rem;
}

* {
    border: none;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: white;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;    
}

.Body-wrapper {
    /* background-color: yellow; */
    max-width: 1366px;
    margin: auto;
    min-height: 100vh;    
}

/* --------------- MAIN -------------------- */

.Main {
    /* border: 1px solid blue; */
    min-height: calc(100vh - var(--header-height) - var(--footer-height-pc));
}

/* --------------- TABLE -------------------- */
.Table {
    border-spacing: 0; 
    border: 1px solid silver;    
    border-radius: 5px;
    display: block;     
}

.Table tbody tr.active {
    background-color: gainsboro;
}

.Table tbody tr:hover {
    cursor: pointer;
}

.Table td {
    padding: 3px 8px;
}

.Table input[type=search] {
    border: 0.5px solid silver;
    border-radius: 4px;
    width: 100%;
    height: 30px;
    padding: 0 5px;
}

.Table-cell-center {
    text-align: center;
}

.Table-cell-end {
    text-align: end;
}

.Table-icon {
    color:rgb(0, 187, 0);
    font-size: 1.5rem;
}

/* -------------- FORM ----------------------------- */

.Form {
    width: 250px; 
    margin-top: 10px;   
}

.Form select,
.Form input:not([type=checkbox])/*:not([type=number])*/,
.Form textarea {
    width: 100%;
    height: 25px;
    border: 1px solid silver;
    border-radius: 5px;
    padding: 0 5px;
}

.Form textarea {
    height: 80px;
    resize: none;
}

.Form input[type=number] {
    width: 25%;
}

.Form input[type=checkbox] {
    margin-left: 10px;    
}

.Form-number-pair {
    /* border: 1px solid red; */
    display: flex;
}

.Form-input-pair-inline {
    display: flex;
}

.Form-input-pair-inline > div:first-child {
    /* border: 1px solid red; */
    width: 75%;
}

.Form-input-pair-inline > div:last-child {
    /* border: 1px solid blue; */
    width: 25%;
}

.FormCard {
    /* border: 1px solid red; */
    width: 320px;
    margin-top: 70px;
    padding: 20px;
    text-align: center;
    box-shadow: 1px 1px 3px black;
    border-radius: 4px;
}

.FormCard input {
    width: 100%;
    height: 40px;
    border: 0.5px solid silver;
    margin-top: 10px;
    padding: 0 15px;
    font-size: 1.1rem;
}

.FormCard input[type=submit] {
    background-color: black;
    color: white;
}

.FormCard input[type=submit]:hover {
    cursor: pointer;
}

.FormCard p {
    margin-top: 20px;
}

/*-------- BUTTONS -------*/ 
.Btn {
    width: 70px;
    margin: 0 5px;
    padding: 10px;
    border-radius: 4px;
    box-shadow: 1px 1px 3px grey;
}

.Btn-accept {
    width: 150px;
}

.Btn:hover {
    cursor: pointer;
}

/*-------------- CARD -------------*/
.Wrapper-flex {
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    padding: 20px 0;
}

.Card {    
    border: 0.5px solid silver;
    background-color: white;
    text-align: center;
    padding: 20px;
    border-radius: 4px;
    box-shadow: 1px 1px 3px silver;    
}

.Card-img-container {
    height: 200px; 
    padding: 0 30px;
    overflow: hidden;
}

.Card-img-container img {
    object-fit: cover;
    border: 1px solid silver;
    border-radius: 5px;
    box-shadow: 0 0 3px silver;
}