/* -------------- WRAPPER TABLE-DETAIL ------------------ */
.Orders-table-detail {
    /* border: 1px solid red; */
    margin-top: 30px;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
}


/* ----------- TABLE ---------------- */

.Orders-table {
    display: block;
    min-width: 320px;
    padding: 5px;
    border: 1px solid silver;
    border-radius: 5px;
    margin: 0 10px;
    border-spacing: 0;
}

.Orders-input {
    border: 1px solid gainsboro;
    width: 70%;
    height: 25px;
    padding: 0 5px;
}

.Orders-date {
    width: 35%;
}

.Orders-table tr {
    width: 100%;
    border-radius: 4px;
    box-shadow: 0 0 3px silver;
}

.Orders-table tbody tr.active {
    background-color: silver;
}

.Orders-table tbody tr:hover {
    cursor: pointer;
}

.Orders-table td {
    padding: 3px;
}

.Orders-col-number {
    text-align: right;
}

.Orders-col-text {
    text-align: left;
}

.Orders-label {
    background-color: rgb(233, 233, 233);
    display: block;
    text-align: center;
    border-radius: 3px;
    cursor: default;
}


/* --------- CARD DETAIL ----------- */

.Orders-detail {
    width: 320px;
}

/* ----------- TABLE ITEMS --------------- */
.Orders-table-items {
    border-spacing: 0;    
}

.Orders-table-items caption:not(caption:first-child) {
    text-align: left;
}

.Orders-table-items td {
    padding: 3px;
    vertical-align: text-top;      
}

.Orders-wrapper-qty {
    border: 1px solid gainsboro;
}

.Orders-icon {
    cursor: pointer;
    font-size: .75rem;
    font-weight: bold;
}

.Orders-table-items tfoot td{
    border-top: 1px solid silver;
}

.Orders-icon-delete {
    color: rgb(253, 64, 43);
    font-size: 1.25rem;
    position: relative;
    top: 3px; 
    cursor: pointer;   
}