/* -------------- WRAPPER TABLE-DETAIL ------------------ */
.OrdersCustomer-table-detail {
    /* border: 1px solid red; */
    margin-top: 30px;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
}


/* ----------- TABLE ---------------- */

.OrdersCustomer-table {
    display: block;
    /* width: 320px; */
    padding: 5px;
    border: 1px solid silver;
    border-radius: 5px;
    margin: 0 10px;
    border-spacing: 0;
}

.OrdersCustomer-table tr {
    width: 100%;
    border-radius: 4px;
    box-shadow: 0 0 2px silver;
}

.OrdersCustomer-table tbody tr.active {
    background-color: silver;
}

.OrdersCustomer-table tbody tr:hover {
    cursor: pointer;
}

.OrdersCustomer-table input {
    border: 1px solid gainsboro;    
}

.OrdersCustomer-state {
    background-color: rgb(238, 238, 238);
    display: block;
    width: 100px;
    text-align: center;
    border-radius: 3px;
}

.OrdersCustomer-table td {
    padding: 3px;    
}

.OrdersCustomer-col-number {
    text-align: right;
}

.OrdersCustomer-col-text {
    text-align: left;
}


/* --------- CARD DETAIL ----------- */

.OrdersCustomer-detail {
    width: 320px;
}

/* ----------- TABLE ITEMS --------------- */
.OrdersCustomer-table-items {
    border-spacing: 0;    
}

.OrdersCustomer-table-items td {
    padding: 3px;
    vertical-align: text-top;      
}

.OrdersCustomer-table-items tfoot td{
    border-top: 1px solid silver;
}