.FormMessageWrapper {
    background-color: salmon;
    margin-top: 15px;
    padding: 15px 5px;
    position: relative; /* sidebar is z-index=1 */      
    border-radius: 4px;
    text-align: center;
}

.FormMessageWrapper.success {
    background-color: lightgreen;
}

.FormMessageIcon {
    background-color: white;
    font-size: 1.2rem;
    position: absolute;
    top: 1px;
    right: 1px;
    border-radius: 50%;
}

.FormMessageIcon:hover {
    cursor: pointer;
}
