.ModalAddArticle-background {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.75);
    /*display: flex;*/
    display: none;  
    justify-content: center;
    align-items: flex-start; 
    overflow: auto;  
}

.ModalAddArticle-background.active {
    display: flex;    
}

.ModalAddArticle-card {
    width: 320px;
    background-color: white;
    margin-top: 20px;
    padding: 20px 5px;
    border-radius: 5px;
    box-shadow: 1px 1px 3px silver;
    overflow: auto;
}

.ModalAddArticle-section-select {
    margin-top: 25px;
}

.ModalAddArticle-card select {
    border: 1px solid gainsboro;
    font-size: 1rem;
}

.ModalAddArticle-card p {
    margin-top: 20px;
}

/* ------------- TABLE --------------- */
.ModalAddArticle-table {
    margin-top: 10px;
    overflow: auto;
}

.ModalAddArticle-table tbody tr {
    box-shadow: 0 0 1px black;
}

.ModalAddArticle-table th,
.ModalAddArticle-table td {
    /* border: 1px solid red; */
    vertical-align: text-top;    
}

.ModalAddArticle-col-number {
    text-align: right;
}

/* ---------------- BUTTONS --------------- */

.ModalAddArticle-btn-add {
    background-color: dodgerblue;
    color: white;
    padding: 5px;
    border-radius: 3px;
    cursor: pointer;
}

.ModalAddArticle-actions {
    margin-top: 20px;    
    text-align: center;
}