.Header {
    background-color: white;
    height: var(--header-height);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    box-shadow: 0 0 3px gainsboro;
}

.Header.isUserSession {
    background-color: rgb(255, 197, 202);
}

.Header.isCustomerSession {
    background-color: rgb(235, 255, 235);
}

.Header-menu-categories {
    background-color: black;
    /* color: white; */
    border: 1px solid black;
    width: 300px;
    height: 100vh;
    position: fixed;
    top: 0;
    left: -300px;
    transition: left 500ms;
    z-index: 1; /* for components: position=relative */
}

.Header-menu-categories.active {
    left: 0;
    transition: left 500ms;
}

.Header-icon-close {
    color: white;
    font-size: 1.75rem;
    margin-left: 112px;
    margin-top: 13px;
    cursor: pointer;
}

.Header-icon-burguer,
.Header-icon-user,
.Header-icon-search,
.Header-icon-cart,
.Header-icon-menu-customer {
    color: black;
    font-size: 1.75rem;
    cursor: pointer;
}

.Header-icon-search,
.Header-icon-cart,
.Header-icon-menu-customer {
    margin-left: 8px;
}

.Header-icon-search {
    cursor: pointer;
}

/* --------- cart and circle counter ------------------- */
.Header-container-cart {
    /* border: 1px solid blue; */
    display: inline-flex;
    position: relative;
}

/* --------- circle counter items of cart --------- */
.Header-container-cart span {
    background-color: red;
    color: white;
    width: 20px;
    height: 20px;
    padding-top: 1px;
    text-align: center;
    font-size: 0.8rem;
    border-radius: 50%;
    position: absolute;
    top: -5px;
    right: -5px;
}

.Header-username,
.Header-customeremail {
    position: absolute;
    right: 5px;
    top: 60px;
}

.Header-list {
    list-style: none; 
    margin: 50px 30px 0 30px;
}

.Header-link-item {    
    text-decoration: none;    
}

.Header-item {
    color: white;
    line-height: 1.5rem;
    padding: 10px 5px;
}

.Header-item:hover {
    color: black;
    background-color: gainsboro;
    border-radius: 3px;
}

.Header-menu-categories hr,
.Header-menu-user hr,
.Header-menu-customer hr {
    background-color: gray;
    height: 1px;
}

.Header-menu-categories p,
.Header-menu-user p,
.Header-menu-customer p {
    color: grey;
    margin-left: 30px;
}

/* -------------- RIGHT SIDEBAR USER MENU ---------------- */
