.Header-finder {
    /* border: 1px solid red; */
    background-color: rgb(245, 245, 245);
    width: 100%; 
    max-width: 1366px;
    line-height: var(--products-title-height);
    font-size: 1.5rem;
    box-shadow: 0 0 3px gainsboro;
    padding: 0 40px;
    display: flex;
    justify-content: space-between; 
    align-items: center; 
    position: absolute; 
}

.Header-finder input {
    border: 0;
    background-color: transparent;
    color: grey;
    line-height: calc(var(--products-title-height) - 2px);
    font-size: 1.5rem;
    margin-top: 1px;  
    width: 90%;
}

.Header-finder-icon {
    color: grey;
    font-size: 2rem;    
}

.Header-finder-icon.close {
    cursor: pointer;
}