.Modal-information {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.75);
    justify-content: center;
    align-items: center;
    /*display: flex;*/
    display: none;    
}

.Modal-information.active {
    display: flex;
}

.Modal-card {
    background-color: white;
    text-align: center;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 1px 1px 3px silver;
}

.Modal-card p {
    margin-top: 20px;
}

.Modal-actions {
    margin-top: 20px;    
}