.Detail-top {
    border: 1px solid silver;
}

.Detail-icon {
    font-size: 1.5rem;
    margin: 10px;
}

.Detail-icon:hover,
.Detail-btn-cart {
    cursor: pointer;
}

.Detail-card {
    width: 45%;    
}

.Detail-container {
    height: 480px;    
}

.Detail-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.Detail-data {
    text-align: start;
}

.Detail-title,
.Detail-price {
    font-size: 1.3rem;
    margin-top: 20px;
    font-weight: 500;
}

.Detail-price {
    color: grey;
}

.Detail-description {
    font-size: 1.2rem;
}

.Detail-qty {
    border: 1px solid silver;
    border-radius: 5px;
    display: inline;
    width: 110px;
    margin-top: 20px;
    line-height: 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;    
}

.Detail-stock {
    border: 1px solid silver;
    border-radius: 5px;
    margin-top: 20px;
    line-height: 3rem;
    text-align: center;
    color: grey;
}

.Detail-btn-cart {
    background-color: black;
    color: white;
    display: block;
    width: 100%;
    line-height: 3rem;
    margin-top: 20px;
    border-radius: 5px;
}

.Detail-zoom:hover {
    cursor: pointer;
}

.Detail-zoom:after {
    opacity: 0.8;
}